import { Axios } from "axios";
import React from "react";
import { useEffect, useState } from "react";

export function JWT(props){
    let correct = props.correct;
    const [allUsers, setAllUsers] = useState(0);
    const [allWinners, setAllWinners] = useState(0);
    const sendResults = async () => {
       fetch(`/api/postResults`, {
           method: "POST",
           body: correct,
       }).catch((error) => ("Something went wrong"));
      }

    const getResults = async () => {
        Axios.get(`api/getResults`).then((response) => {
            console.log(response);
        })
    }
      useEffect(() => {
          sendResults();
          getResults();
      })

      return (
          <div>TOTAL PLAYERS: {allUsers} TOTAL SOLVED: {allWinners}</div>
      )
}